.pending{
	position:fixed;
	top:0;
	left:0;
	right:0;
	bottom:0;
	width:100%;
	height:100vh;
	z-index:9999;
	background: rgba(46, 55, 66, 0.5);
	overflow:hidden;
	display:none;
	align-items:center;
	justify-content:center;
	 
}

.pending.show{
	display: flex;
}

.pending .centred-end{
    display: flex;
    align-items: flex-end;
    justify-content: center;
}
.pending .message{
	font-family: Roboto;
  font-size: 40px;
  font-weight: 300;
  text-align: center;
  color: white;
  margin-right:16px;
}

.pending img{
	will-change: content;
	height: 76px;
}

